import { parseObjectToGetUrl } from '../util'
import $axios from './http'

// 登录验证
export function login(params) {
  const url = 'login_auth'
  params = {
    operate_mode: 'business',
    ...params
  }
  return $axios.post(url, params)
}

// 列出全部用户信息
export function loginOperate(params) {
  const url = 'login_operate'
  return $axios.post(url, params)
}

// 获取验证码
export function forgetPassword(params) {
  const url = 'forget_password'
  return $axios.post(url, params)
}

// 完成重置密码
export function forgetPassword2(params) {
  const url = 'forget_password2'
  return $axios.post(url, params)
}

// 商家子账户注册
export function ramCreate(params) {
  const url = 'ram_create'
  return $axios.post(url, params)
}

// 商家子账号修改
export function alterRam(params) {
  const url = 'alter_ram';
  return $axios.post(url, params)
}

// 商家子账户删除
export function deleteSubaccount(params) {
  const url = 'login_operate'
  const obj = {
    operate_mode: 'delete',
    ...params
  }
  return $axios.post(url, obj)
}
// 二维码
export function qrcode(params) {
  const url = 'qrcode'
  return $axios.get(url, params)
}

// 获取所有子账户
export function getSubStoreList(params) {
  const url = 'login_operate'
  const data = {
    operate_mode: 'get_sub_user',
    product_data: {
      role: '2',
      ...params,
    },
  }
  return $axios.post(url, data)
}

// 记住登陆状态
export function rememberLoginStatus(params) {
  const url = 'remember_login'
  let ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

// 获取所有商铺名单
export function getAllShop() {
  const url = 'get_shop_list'
  return $axios.get(url)
}
