import $axios from './http'
import {
  parseObjectToGetUrl
} from '@/util'

export function getProductCategories(params) {
  const url = 'get_product_categories'
  let ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

export function getProductList(params) {
  const url = 'get_product_list'
  let ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

export function getProductInfo(params) {
  const url = 'get_product_info'
  let ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

// 修改商品
export function modifyCommodity(params) {
  const url = 'operate_product'
  const mode = {
    operate_mode: 'alter',
  }
  params = {
    ...mode,
    product_data: { ...params }
  }
  return $axios.post(url, params)
}

// 添加商品
export function addCommodity(params) {
  const url = 'operate_product'
  const mode = {
    operate_mode: 'insert',
  }
  params = {
    product_data: { ...params },
    ...mode
  }
  return $axios.post(url, params)
}

// 删除商品
export function deleteCommodity(params) {
  const url = 'operate_product'
  const mode = {
    operate_mode: 'delete',
  }
  params = {
    ...params,
    ...mode
  }
  return $axios.post(url, params)
}
