import axios from 'axios'
import { parseObjectToGetUrl } from '../util'
import Cookies from 'js-cookie'

var isPro = process.env.NODE_ENV === 'production'

const $axios = axios.create({
  // timeout: 10000,
  baseURL: isPro ? 'https://qauovjfr97.execute-api.ap-southeast-2.amazonaws.com/default/' : '/api',
})

$axios.interceptors.request.use(
  (config) => {
    config.headers['3RD_SESSION'] =
      (localStorage.getItem('token') || Cookies.get('token'))
    return config
  },
  (err) => {
    return Promise.reject(err)
  },
)

$axios.interceptors.response.use(
  (response) => {
    return response
  },
  (err) => {
    return Promise.reject(err.response)
  },
)


export function fetchGet(url, params) {
  return new Promise((resolve, reject) => {
    let newParams = params && parseObjectToGetUrl(params)
    $axios.get(url, newParams || '')
      .then(response => resolve(response), err => reject(err)).catch(err => reject(err))
  })
}

export function fetchPost(url, params) {
  return new Promise((resolve, reject) => {
    $axios.post(url, params)
      .then(response => resolve(response), err => reject(err)).catch(err => reject(err))
  })
}


export default $axios
