import { withdrawalData } from '../../api/billAPI'
import { getStatisticsOrderInfo, getProductStatistics, getShopStatistics } from '../../api/statisticsAPI'
const state = () => {
  return {
    shopOrderStatistics: [],
    bussinessStatistics: [],
    commoditySales: [],
    totalOrder: 0
  }
}

const mutations = {
  SET_SHOP_ORDER_STATISTICS(state, payload) {
    state.shopOrderStatistics = payload
  },
  SET_BUSINESS_STATISTICS(state, payload) {
    state.bussinessStatistics.splice(0, state.bussinessStatistics.length)
    state.bussinessStatistics.push(payload)
  },
  SET_COMMODITY_SALES(state, payload) {
    state.commoditySales = payload
  },
  SET_TOTAL_ORDER(state, payload) {
    state.totalOrder = payload
  }
}

const actions = {
  // 获取商家订单信息汇总
  async sGetShopStatistics({ commit }, payload) {
    let ret = await getShopStatistics(payload)
    commit('SET_BUSINESS_STATISTICS', ret.data)
  },

  // 获取每件商品销售信息
  async getProductStatistics({ commit }, payload) {
    let ret = await getProductStatistics(payload)
    commit('SET_COMMODITY_SALES', ret.data)
  },

  // 商家订单信息筛选统计
  async sGetStatisticsOrderInfo({ commit }, payload) {
    let { obj, method } = payload
    let ret = await getStatisticsOrderInfo(obj)
    if (ret.status === 200) {
      // commit('SET_BUSINESS_STATISTICS', ret.data)
      commit('SET_SHOP_ORDER_STATISTICS', { data: ret.data.data, method })
      commit('SET_TOTAL_ORDER', ret.data.total_num)
    }
  },

  // 根据年月获取历史提现信息
  async getWithdrawalData({ commit }, payload) {
    let { obj, method } = payload
    let ret = await withdrawalData(obj)
    if (ret.status === 200) {
      commit('SET_SHOP_ORDER_STATISTICS', { data: ret.data.data, method })
      commit('SET_TOTAL_ORDER', ret.data.total_num)
    }
  }
}
export default {
  namespaced: true,
  state,
  mutations,
  actions
}