import { userAPI, storeAPI } from '@/api'
import router from '@/router'
import Cookies from 'js-cookie'
import { getShopInfo } from '../../api/storeAPI'
import { alterRam, rememberLoginStatus } from '../../api/userAPI'
const state = () => {
  return {
    token: '',
    data: {},
    subAccount: [],
    loginData: {},
    tableCount: []
  }
}

const mutations = {
  SET_TOKEN(state, preload) {
    state.token = preload
  },
  SET_USER_DATA(state, preload) {
    state.data = preload
  },
  SET_SUB_ACCOUNT(state, preload) {
    state.subAccount = preload
  },
  ADD_SUB_ACCOUNT(state, preload) {
    state.subAccount.push(preload)
  },
  DELETE_SUB_ACCOUNT(state, preload) {
    let obj = state.subAccount.filter(item => item.id === preload)
    state.subAccount.splice(state.subAccount.indexOf(...obj), 1)
  },
  ALTER_SUB_ACCOUNT(state, payload) {
    let { id } = payload
    let item = state.subAccount.filter(item => item.id === id)
    let index = state.subAccount.indexOf(item)
    state.subAccount.splice(index, 1, payload)
  },
  SET_LOGIN_DATA(state, payload) {
    state.loginData = payload
  },
  CHANGE_BALANCE(state, payload) {
    state.loginData.balance -= payload
  },
  SET_TABLE_COUNT(state, payload) {
    let array = []
    for (let i = 0; i < payload; i++) {
      array.push({ id: i, loading: false })
    }
    state.tableCount = array
  },
  ADD_TABLE_COUNT(state) {
    state.tableCount.push({ id: state.tableCount.length, loading: false })
  },
  MINIS_TABLE_COUNT(state) {
    if (state.tableCount.length === 1) return
    state.tableCount.splice(state.tableCount.length - 1, 1)
  },
  ALTER_TABLE_LOADING(state, payload) {
    let { id, loading } = payload
    state.tableCount.splice(id, 1, { id, loading: loading })
  }
}
const actions = {
  // login
  async login({ commit }, preload) {
    if (Cookies.get('token')) {
      let email = Cookies.get('login_email')
      if (email === preload.email && preload.password === '********') {
        let ret = await rememberLoginStatus({ email })
        if (ret.status === 200) {
          commit('SET_TOKEN', ret.data.token)
          commit('SET_USER_DATA', ret.data['login_data'])
          commit('SET_LOGIN_DATA', ret.data['user_data'])
          commit('SET_TABLE_COUNT', ret.data['user_data'].table_count)
          localStorage.setItem('token', ret.data.token)
          if (!Cookies.get('checkBoxStatus')) {
            Cookies.remove('token')
            Cookies.remove('login_email')
          }
          router.push('/com')
        }
      } else {
        // 输入了新的账号
        let ret = await userAPI.login(preload)
        if (ret.status === 200) {
          commit('SET_TOKEN', ret.data.token)
          commit('SET_USER_DATA', ret.data['login_data'])
          commit('SET_LOGIN_DATA', ret.data['user_data'])
          commit('SET_TABLE_COUNT', ret.data['user_data'].table_count)
          localStorage.setItem('token', ret.data.token)
          Cookies.set('token', ret.data.token, { expires: 7 })
          Cookies.set('login_email', ret.data.login_data.email, { expires: 7 })
          router.push('/com')
        }
      }

    } else {
      // 点击了记住checkBoxStatus
      if (Cookies.get('checkBoxStatus')) {
        let ret = await userAPI.login(preload)
        if (ret.status === 200) {
          commit('SET_TOKEN', ret.data.token)
          commit('SET_USER_DATA', ret.data['login_data'])
          commit('SET_LOGIN_DATA', ret.data['user_data'])
          commit('SET_TABLE_COUNT', ret.data['user_data'].table_count)
          localStorage.setItem('token', ret.data.token)
          Cookies.set('token', ret.data.token, { expires: 7 })
          Cookies.set('login_email', ret.data.login_data.email, { expires: 7 })
          router.push('/com')
        }
      } else {
        let ret = await userAPI.login(preload)
        if (ret.status === 200) {
          commit('SET_TOKEN', ret.data.token)
          commit('SET_USER_DATA', ret.data['login_data'])
          commit('SET_LOGIN_DATA', ret.data['user_data'])
          commit('SET_TABLE_COUNT', ret.data['user_data'].table_count)
          localStorage.setItem('token', ret.data.token)
          router.push('/com')
        }
      }
    }
  },
  // 商家子账号获取
  async getSubAccount({ commit }, preload) {
    let ret = await userAPI.getSubStoreList(preload)
    commit('SET_SUB_ACCOUNT', ret.data)
  },
  // 商家子账号注册
  async registerSubAccount({ commit }, preload) {
    let ret = await userAPI.ramCreate(preload)
    commit('ADD_SUB_ACCOUNT', ret.data)
  },
  // 商家子账户删除
  async deleteSubAccount({ commit }, payload) {
    let id = payload.product_data.id
    await userAPI.deleteSubaccount(payload)
    commit('DELETE_SUB_ACCOUNT', id)
  },

  // 商家子账户修改
  async alterSubAccount(_, payload) {
    await alterRam(payload)
  },

  // 修改店铺营业状态
  async setBussinessStatus({ commit }, payload) {
    await storeAPI.setBussinessStatus(payload)
    commit()
  },

  // 修改商家基本信息
  async alterBussiness({ commit }, payload) {
    let ret = await storeAPI.alterShopInfo(payload)
    if (ret.status === 200) {
      commit('SET_LOGIN_DATA', payload)
    }
  },

  // 获取商家基本信息
  async getShopBasicInfo({ commit }, payload) {
    let ret = await getShopInfo(payload)
    if (ret.status === 200) {
      commit('SET_LOGIN_DATA', ret.data)
    }
  },

  // 修改商家图片
  async setStorePic(_, payload) {
    await storeAPI.setStorePic(payload)
  },

}
export default {
  namespaced: true,
  state,
  mutations,
  actions,
}
