export function childrenRoutes(name) {
  for (let index = 0, length = routes.length; index < length; index += 1) {
    const item = routes[index]
    if (item.children && item.name === name && item.children[0].children) {
      return {
        menuItemRoutes: item.children[0].children,
        baseUrl: item.children[0].redirect.split('/').slice(0, -1).join('/'),
      }
    }
  }
}


export function resolveRoutes(name) {
  for (let index = 0, length = routes.length; index < length; index += 1) {
    let item = routes[index]
    if (name === 'Commodity') {
      return item.children[0].redirect
    } else {
      if (item.children && item.name === name) {
        return item.children[0].children && item.children[0].children.length > 0 ?
          item.children[0].redirect :
          item.redirect
      }
    }
  }
}

export function parseObjectToGetUrl(params) {
  let ret = []
  for (const key in params) {
    ret.push(`${key}=${params[key]}`)
  }
  return '?' + ret.join('&')
}

// 将obj中arr列出的属性返回一个新对象
export function copyObjectProperties(obj, arr) {
  return arr.reduce((acc, cur) => {
    cur in obj && (acc[cur] = obj[cur])
    return acc
  }, {})
}

// 日期的格式化
export function formatDate(fmt) {
  let date = new Date()
  var o = {
    'M+': date.getMonth() + 1,
    "d+": date.getDate(), // 日
    "h+": date.getHours(), // 小时
    "m+": date.getMinutes(), // 分
    "s+": date.getSeconds(), // 秒
    "q+": Math.floor((date.getMonth() + 3) / 3), // 季度
    "S": date.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o) {
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
      return fmt;
    }
  }
}

// input框不能输入小数
export function handlerInput(number) {
  let str = '' + number;
  if (str.indexOf('.') === -1) {
    str.split()
  }
}

// 判断两个数组是否完全相同
export function equal(a, b) {
  if (a.length !== b.length) {
    return false;
  } else {
    for (let i = 0; i < a.length; i++) {
      if (a[i] !== b[i]) {
        return false
      }
    }
    return true
  }
}

// 获取loading数组
export function getLoadingArray(value) {
  let newArray = [];
  if (value) {
    value.forEach((item) => {
      newArray.push({ loading: item.loading || false })
    })
  }
  return newArray;
} 