
import { getDiscountInfo, alterDiscountInfo, alterReductionInfo, getReductionInfo, deleteDiscount, deleteReduction, addReductionInfo } from '../../api/storeAPI'
const state = () => {
  return {
    discountInfo: [],
    reductionInfo: [],
    saleStatus: ''
  }
}

const mutations = {
  SET_DISCOUNT(state, payload) {
    state.discountInfo = payload
  },
  SET_REDUCTION(state, payload) {
    state.reductionInfo = payload
  },
  SET_SALE_STATUS(state, payload) {
    state.saleStatus = payload
  },
  ALTER_DISCOUNT(state, payload) {
    let { obj, index } = payload
    state.discountInfo[index] = obj
  },
  DELETE_DISCOUNT(state, payload) {
    console.log(payload);
    state.discountInfo.splice(payload, 1)
  },
  DELETE_REDUCTION(state, payload) {
    state.reductionInfo.splice(payload, 1)
  },
  SET_DISCOUNT_LOADING(state, payload) {
    let { index, status } = payload
    state.discountInfo[index].loading = status
  },
  SET_REDUCTION_LOADING(state, payload) {
    let { index, status } = payload
    state.reductionInfo[index].loading = status
  }
}
const actions = {
  async getDiscountInfo({ commit }, payload) {
    let ret = await getDiscountInfo(payload)
    let newArray = ret.data.map(item => { return { ...item, loading: false } })
    commit('SET_DISCOUNT', newArray)
  },
  async getReductionInfo({ commit }, payload) {
    let ret = await getReductionInfo(payload)
    let newArray = ret.data.map(item => { return { ...item, loading: false } })
    commit('SET_REDUCTION', newArray)
  },
  async alterDiscountInfo(_, payload) {
    let { obj } = payload;
    await alterDiscountInfo(obj)
    // commit('ALTER_DISCOUNT', payload)
  },
  async alterReductionInfo(_, payload) {
    await alterReductionInfo(payload)
    // commit('SET_REDUCTION', ret.data)
  },
  async deleteDiscount({ commit }, payload) {
    let { obj, index } = payload
    let ret = await deleteDiscount(obj)
    if (ret.status === 200) {
      commit('DELETE_DISCOUNT', index)
    }
  },
  async deleteReduction({ commit }, payload) {
    let { obj, index } = payload
    let ret = await deleteReduction(obj)
    if (ret.status === 200) {
      commit('DELETE_REDUCTION', index)
    }
  },
  async addReductionInfo(_, payload) {
    await addReductionInfo(payload)
    // commit('ADD_REDUCTION', ret.data)
  }
}


export default {
  namespaced: true,
  state,
  actions,
  mutations
}