import { readedWithdrawalInfo, readWithdrawalInfo, unreadedWithdrawalInfo, withdrawal } from "../../api/billAPI"

const state = () => {
    return {
        readedWithdrawalList: [],
        unreadedWithdrawalList: []
    }
}

const mutations = {
    SET_READ_WITHDRAWAL_LIST(state, payload) {
        state.readedWithdrawalList = payload
    },
    SET_UNREAD_WITHDRAWAL_LIST(state, payload) {
        state.unreadedWithdrawalList = payload
    },
    CHANGE_WITHDRAWAL_READ_STATUS(state, payload) {
        const item = state.unreadedWithdrawalList.filter(item => item.id === payload[0].id)
        const index = state.unreadedWithdrawalList.indexOf(item)
        state.unreadedWithdrawalList.splice(index, 1)
        state.readedWithdrawalList.unshift(payload[0])
    }
}

const actions = {
    // 获得未读提款记录
    async sGetUnreadWithdrawalInfo({ commit }, payload) {
        let ret = await unreadedWithdrawalInfo(payload)
        if (typeof ret.data !== 'string') {
            commit('SET_UNREAD_WITHDRAWAL_LIST', ret.data)
        }
        return ret.data;
    },
    // 获得已读提款记录
    async sGetReadWithdrawalInfo({ commit }, payload) {
        let ret = await readedWithdrawalInfo(payload)
        commit('SET_READ_WITHDRAWAL_LIST', ret.data)
        return ret.data;
    },
    // 提款
    async sWithdrawal(_, payload) {
        await withdrawal(payload)
    },
    // 查看未读信息
    async sReadWithdrawalInfo(_, payload) {
        let ret = await readWithdrawalInfo(payload)
        // commit('CHANGE_WITHDRAWAL_READ_STATUS', ret.data)
        return ret.data
    },


}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}