import $axios from './http'
import {
  parseObjectToGetUrl
} from '@/util'

// 获取商家基本信息
export function getShopInfo(params) {
  const url = 'get_shop_info'
  const ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

// 修改店铺基本信息
export function alterShopInfo(params) {
  const url = 'operate_shop'
  const obj = {
    operate_mode: 'alter',
    shop_data: params
  }
  return $axios.post(url, obj)
}

// 获取店铺折扣信息
export function getDiscountInfo(params) {
  const url = "get_discount_info"
  const ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

// 修改折扣信息
export function alterDiscountInfo(params) {
  const url = 'set_product_discount'
  return $axios.post(url, params)
}

