// 添加满减信息
export function addReductionInfo(params) {
  const url = 'operate_reduction'
  params = {
    operate_mode: 'insert',
    ...params
  }
  return $axios.post(url, params)
}

// 获取满减信息
export function getReductionInfo(params) {
  const url = 'get_reduction_info'
  const ret = parseObjectToGetUrl(params)
  return $axios.get(url + ret)
}

// 修改满减信息
export function alterReductionInfo(params) {
  const url = 'operate_reduction'
  params = {
    "operate_mode": "alter",
    ...params
  }
  return $axios.post(url, params)
}

// 删除满减信息
export function deleteReduction(params) {
  const url = 'operate_reduction'
  params = {
    operate_mode: 'delete',
    ...params
  }
  return $axios.post(url, params)
}

// 删除折扣信息
export function deleteDiscount(params) {
  const url = 'delete_discount'
  return $axios.post(url, params)
}

// 修改店家营业状态
export function setBussinessStatus(params) {
  const url = 'set_business_status'
  return $axios.post(url, params)
}

// 修改商家图片
export function setStorePic(params) {
  const url = 'oss_shop_pictuer'
  return $axios.post(url, params)
}

// 修改商家基本信息
export function alterBusiness(params) {
  const url = 'alter_business'
  return $axios.post(url, params)
}


// 类别display_order修改
export function alterSortOrder(params) {
  const url = 'operate_product_categories'
  const obj = {
    operate_mode: "moveUp",
    category_info: params
  }
  return $axios.post(url, obj)
}
