<template>
  <div>
    <router-link :to="route">
      <el-menu-item class="sideBar" :index="route.split('/')[1]">
        <i class="iconfont" :class="key[routes.name]"></i>
        <span>{{ generateRouteTitle(routes.name) }}</span>
        <i class="el-icon-arrow-right arrow-right"></i>
      </el-menu-item>
      <!--<el-divider class="divider"></el-divider>-->
    </router-link>
  </div>
</template>

<script>
import { resolveRoutes } from "@/util";
import { generateRouteTitle } from "@/util/i18n.js";
export default {
  created() {},
  props: {
    routes: {
      type: Object,
    },
  },
  data() {
    return {
      key: {
        News: "icon-xinwen",
        Store: "icon-icon-test",
        Home: "icon-shangpin",
        Data: "icon-shuju",
        Bill: "icon-icon--",
        Discount: "icon-zhekou",
        "Sub-account": "icon-account",
        "QR code": "icon-ico",
      },
    };
  },
  methods: {
    generateRouteTitle,
  },
  computed: {
    route() {
      return resolveRoutes(this.routes.name);
    },
  },
};
</script>

<style lang="scss" scoped>
.iconfont {
  padding-right: 1em;
}

.sideBar {
  margin: 0 20px;
}

.sideBar.is-active {
  color: $--color-primary;
  background-color: rgba(254, 121, 34, 0.2);
}

.divider {
  margin: 10px 0;
}
</style>
